<!--eslint-disable-->
<template>
  <div>
    <div class="slideshow-wrapper" v-for="s in servico" :key="s.id">
      <div id="slideshow">
        <div class="slide">
          <div class="copy">
            <div class="title">{{ s.title }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="info-wrapper" v-for="s in servico" :key="s.id">
      <!-- <h1 class="servico-title">{{s.title}}</h1> -->
      <div class="info">
        <div class="tabs">
          <div class="tab t0">
            <div class="txt">
              Sobre a
              <span class="especialidade">{{ s.sigla }}</span>
            </div>
          </div>
          <div class="tab t1">
            <div class="txt">Porquê?</div>
          </div>
          <div class="tab t2">
            <div class="txt">
              Queríamos muito
              <br />partilhar consigo
            </div>
          </div>
        </div>
        <div class="topics">
          <div class="topic t0" v-html="s.about"></div>
          <div class="topic t1" v-html="s.why"></div>
          <div class="topic t2" v-html="s.sharing"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";
// import FadeIn from "../../../animations/fade";

export default {
  data() {
    return {
      // FadeIn,
      title: "",
      servico: [
        {
          title: "",
          sigla: "",
          about: "",
          why: "",
          sharing: "",
        },
      ],
    };
  },
  metaInfo() {
    return {
      title: this.title,
      meta: [
        {
          name: "description",
          content: "",
        },
        {
          property: "og:title",
          content: "",
        },
        {
          property: "og:title",
          content: "",
        },
        {
          property: "og:description",
          content: "",
        },
      ],
    };
  },
  mounted() {
    var vm = this;

    var tab0 = $(".tab.t0");
    var tab1 = $(".tab.t1");
    var tab2 = $(".tab.t2");

    $(".topic.t0").css("pointer-events", "all");
    $(".topic.t1").css("pointer-events", "none");
    $(".topic.t2").css("pointer-events", "none");

    tab0.click(function () {
      $(".topic.t0").animate({ opacity: "1" }, 0);
      $(".topic.t1").animate({ opacity: "0" }, 0);
      $(".topic.t2").animate({ opacity: "0" }, 0);

      $(".topic.t0").css("pointer-events", "all");
      $(".topic.t1").css("pointer-events", "none");
      $(".topic.t2").css("pointer-events", "none");

      var t0 = document.querySelector(".topic.t0");
      $(".topics").css("height", t0.clientHeight);

      if (window.innerWidth < 600) {
        // document.querySelector(".topics").scrollIntoView(true);
        $([document.documentElement, document.body]).animate(
          {
            scrollTop: $(".topics").offset().top - 100,
          },
          0
        );
      }
    });

    tab1.click(function () {
      $(".topic.t0").animate({ opacity: "0" }, 0);
      $(".topic.t1").animate({ opacity: "1" }, 0);
      $(".topic.t2").animate({ opacity: "0" }, 0);

      $(".topic.t0").css("pointer-events", "none");
      $(".topic.t1").css("pointer-events", "all");
      $(".topic.t2").css("pointer-events", "none");

      var t1 = document.querySelector(".topic.t1");
      $(".topics").css("height", t1.clientHeight);

      if (window.innerWidth < 600) {
        // document.querySelector(".topics").scrollIntoView(true);
        $([document.documentElement, document.body]).animate(
          {
            scrollTop: $(".topics").offset().top - 100,
          },
          0
        );
      }
    });

    tab2.click(function () {
      $(".topic.t0").animate({ opacity: "0" }, 0);
      $(".topic.t1").animate({ opacity: "0" }, 0);
      $(".topic.t2").animate({ opacity: "1" }, 0);

      $(".topic.t0").css("pointer-events", "none");
      $(".topic.t1").css("pointer-events", "none");
      $(".topic.t2").css("pointer-events", "all");

      var t2 = document.querySelector(".topic.t2");
      $(".topics").css("height", t2.clientHeight);

      if (window.innerWidth < 600) {
        // document.querySelector(".topics").scrollIntoView(true);
        $([document.documentElement, document.body]).animate(
          {
            scrollTop: $(".topics").offset().top - 100,
          },
          0
        );
      }
    });

    var location = window.location.href;
    var split_url = location.split("/");
    var ref = split_url[split_url.length - 1];

    var map = Array.prototype.map;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getServicoInfo.php")
      .then((resp) => {
        const data = resp.data;
        var info = [];

        map.call(data, function (d) {
          if (d.url_code == ref) {
            info.push({
              title: d.title,
              sigla: d.sigla,
              about: d.about,
              why: d.why,
              sharing: d.sharing,
            });

            const noTags = d.about.replace(/(<([^>]+)>)/gi, "");
            var length = 255;
            var trimmedString = noTags.substring(0, length);

            vm.$data.title =
              d.title + " - Ana Carolina Pereira - Clínica de Psicologia";
            vm.$metaInfo.meta[0].content = trimmedString + "...";
            vm.$metaInfo.meta[1].content =
              d.title + " - Ana Carolina Pereira - Clínica de Psicologia";
            vm.$metaInfo.meta[2].content =
              "https://beta.anacarolinapereira.pt/academia/formacao/" +
              d.url_code;
            vm.$metaInfo.meta[3].content = trimmedString + "...";
          }
        });

        vm.$data.servico = info;
      })
      .then(() => {
        var mainTopic = document.querySelector(".topic.t2");
        var topics = document.querySelectorAll(".topic");
        var heights = [];

        Array.prototype.max = function () {
          return Math.max.apply(null, this);
        };

        Array.prototype.min = function () {
          return Math.min.apply(null, this);
        };

        map.call(topics, function (pg) {
          heights.push(pg.clientHeight);
        });

        $(".topics").css("height", heights.max());
        window.addEventListener("resize", function () {
          $(".topics").css("height", heights.max());
        });
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.slideshow-wrapper {
  background-color: $white;
  height: pxToEm(383.5);
  padding: $pd-desktop;
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: 30%;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
    background-size: contain;
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
    background-size: contain;
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
    background-size: contain;
    background-position: top center;
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    background-size: contain;
    background-position: top center;
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    // height: auto;
    padding: 0 pxToEm(20);
    background-size: cover;
    background-position: bottom center;
  }

  #slideshow {
    height: 100%;
    margin: auto;
    max-width: pxToEm(1400);

    .slide {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      justify-content: center;

      .copy {
        position: relative;
        display: flex;
        flex-direction: column;
        width: auto;
        justify-content: space-between;
        align-items: flex-end;

        @media only screen and (max-width: 991px) {
          align-items: center;
          text-align: center;
          top: pxToEm(20);
          padding: 0;
          max-width: 100%;
          background-color: rgba(255, 255, 255, 0.6);
        }

        .title {
          color: $turquiose;
          font-size: pxToEm(32);
          text-align: right;
          font-family: "Dalmatins";

          @media only screen and (max-width: 991px) {
            font-size: pxToEm(28);
          }

          @media only screen and (max-width: 599px) {
            text-align: center;
          }
        }
      }
    }
  }
}

.info-wrapper {
  display: flex;
  height: auto;
  padding: 0 pxToEm(130);
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  // margin-top: pxToEm(100);

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
  }

  @media only screen and (min-width: 300px) and (max-width: 599px) {
    padding: 0 pxToEm(20);
    margin-top: 0;
  }

  h1 {
    text-align: center;
    color: $turquiose;
    margin-bottom: pxToEm(50);
  }

  .info {
    max-width: pxToEm(1400);
    width: 100%;
    margin: auto;
    height: auto;
    overflow: hidden;

    .tabs {
      display: flex;
      width: pxToEm(1000);
      justify-content: space-between;
      position: relative;

      @media screen and (max-width: 599px) {
        // flex-direction: column;
        width: 100%;
      }

      @media screen and (max-width: 435px) {
        flex-direction: column;
        width: 100%;
      }

      .tab {
        width: pxToEm(300);
        height: pxToEm(100);
        border-radius: pxToEm(20);
        // background-color: $turquiose;
        // border: pxToEm(2) solid $turquiose;
        background-color: $white;
        border: pxToEm(2) solid $turquiose;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: 0.5s;

        @media screen and (max-width: 599px) {
          max-width: pxToEm(250);
          width: 100%;
          margin: 0 pxToEm(10);
          margin-bottom: pxToEm(20);
        }

        @media screen and (max-width: 435px) {
          margin-left: 0;
          margin-right: 0;
          width: 99%;
          max-width: unset;
        }

        &:hover {
          background-color: $turquiose;
          border: pxToEm(2) solid $turquiose;
          transition: 0.5s;

          .txt {
            // color: $turquiose;
            color: $white;
            transition: 0.5s;
          }
        }

        .txt {
          // color: #fff;
          color: $turquiose;
          text-align: center;
          font-weight: bold;
          transition: 0.5;
          font-size: pxToEm(25);
          will-change: transform;
        }
      }
    }

    .topics {
      position: relative;
      min-height: pxToEm(500);
      height: pxToEm(500);
      margin: pxToEm(50) 0 pxToEm(70) 0;

      @media screen and (min-width: 600px) and (max-width: 767px) {
        height: pxToEm(500);
      }

      @media screen and (max-width: 599px) {
        min-height: pxToEm(500);
        height: unset;
      }

      .topic {
        position: absolute;
        opacity: 0;
        font-size: pxToEm(22);
        line-height: 1.5em;
        text-align: justify;
        color: #707070;

        // @media screen and (max-width: 599px) {
        //   position: relative;
        // }

        &.t0 {
          opacity: 1;
        }
      }
    }
  }
}
</style>



